export class QuantityDiscountCounter {
    pnt = -1;

    constructor(private discount: GlobalSalesQuantityDiscountComponentDiscount) {}

    use() {
        const { apply,per,percentage } = this.discount;
        const roundTotal = apply+per;
        this.pnt = (this.pnt+1)%roundTotal;
        return (this.pnt<per)?0:percentage;
    }

    reset() {
        this.pnt = -1;
    }
} 

export type QuantityDiscountDefinition = {
    componentName: string;
    variantHash: VariantHash;
    discount: GlobalSalesQuantityDiscountComponentDiscount;
    counter: QuantityDiscountCounter;
};

