    getCollectionTable() {
        const process = () => {
            const collectionTable = this.getCollectionTableById();
            const ret: CollectionTable = {};
            Object.keys(collectionTable).forEach((collectionId) => {
                const collection = collectionTable[collectionId];
                ret[collection.seo.handle] = collection;
            });
            return ret;
        };

        return this._collectionTable = this._collectionTable || process();
    }

    getCollectionTableById() {
        const process = () => {
            const ret: CollectionTable = {};
            if (this.preview) {
                this.preview.collections.forEach((collection) => {
                    const { productIds } = collection;                                    
                    // ret[collection.id] = {
                    //     ...collection,
                    //     bannerImageUrl: collection.banner.image.url,
                    //     products: productIds.map<ConfigProductData>((id) => ({
                    //         ...this.preview.products[id]
                    //     }))
                    // };
                    // delete (ret[collection.id] as any).productIds;
                });
            }
            else {
                collectionsConfig.forEach((collection) => {
                    const { productIds } = collection;
                    ret[collection.id] = {
                        ...collection,
                        bannerImageUrl: `/pre-build/collections/${collection.id}/${collection.prefix}banner.jpg`,
                        products: productIds.map<ConfigProductData>((id) => ({
                            ...productsConfig[id]
                        }))
                    };
                    delete (ret[collection.id] as any).productIds;
                });
            }
            return ret;
        };

        return this._collectionTableById = this._collectionTableById || process();
    }

