                        "description": "Say hello to the Vaporesso xros 3 Mini Pod System – your perfect pocket-sized companion for top-notch vaping experiences. Sporting a sturdy stainless steel chassis, this device is designed to withstand light falls and drops, ensuring your vape journey remains uninterrupted. Inside its compact form, it houses a potent 1000mAh battery, promising extended vape sessions without the constant need to recharge. Coupled with the new xros 3 Pods, you're in for a long-lasting vaping delight. Embrace flexibility with the xros 3 Mini as it welcomes both freebase and salt nicotine e-juices, giving you the freedom to choose your preferred flavor. With a generous 2.0 mL vape juice capacity, it ensures a long-lasting vaping experience that won't leave you dry. Plus, this device is your open invitation to explore a world of flavor as it's compatible with all previous xros Pods. Filling your pod is a breeze with our top-fill design. Simply remove the sleek black cap, refill, and get back to enjoying your vape in no time. The pods are a snap to connect, thanks to the magnetic connection that ensures a secure, stable link. To top it off, the xros 3 Mini features a trendy neon led indicator light, adding a touch of style while enhancing your vaping convenience. Features Here are the features that make the Vaporesso xros 3 Mini a must-have: * Chipset: axon for smooth performance * Compact dimensions: 99mm by 23.6mm by 13.8mm for portability * Powerful 1000mAh battery for extended vape sessions * Constant Voltage Based Output for a consistent vaping experience * Resistance Range: 0.8/1.0ohm suitable for various e-liquids and salts * Durable Zinc-Alloy chassis for increased durability * Charging: Swift and convenient via the Type-C Port * Operation: Easy-to-use draw-activated system * Pod Series: xros 3 Pod Series for a variety of flavors * Fill System: User-friendly top-fill design * Pod Material: Durable pctg * Pod Capacity: Generous 2mL to hold more e-liquid * Pod Connection: Secure and simple magnetic mechanism * Additional Feature: Trendy neon led indicator light for convenience and style includes The package includes: * xros 3 Mini Battery * 1.0ohm xros 3 Mesh Pod * Type-C Charging Cable * Reminder Card * User Manual * Warranty Card",
                        "handle": "xros-3-mini-pod-device"
