    getFilteredProductList(filters: OptionsOptionProductSelectFilter[],sort: OptionsOptionProductSelectSortOptions) {
        const collectionFilters = filters.filter(({type}) => type==="collection") as OptionsOptionProductSelectFilterCollection[];
        const includeTaggedFilters = filters.filter(({type}) => type==="include-if-tagged") as OptionsOptionProductSelectFilterIfTagged[];
        const excludeTaggedFilters = filters.filter(({type}) => type==="exclude-if-tagged") as OptionsOptionProductSelectFilterIfTagged[];
        let products = (() => {
            if (collectionFilters.length>0) {
                const ret: ConfigProduct[] = [];
                collectionFilters.forEach((collectionFilter) => {
                    ret.push(...(this.getCollectionById(collectionFilter.id)?.products || []));
                });                
                return ret;
            }
            else {
                const productTable = this.getProductTable();
                return Object.keys(productTable).map((productId) => productTable[productId]);
            }    
        })();

        const tagsMatch = (tags: string[],fromTags: string[]) => {
            const hash: {[value: string]: true} = {};
            fromTags.forEach((tag) => hash[tag] = true);
            for(let i=0;i<tags.length;i++) {
                if (hash[tags[i]]) return true;
            }
            return false;
        };

        if (includeTaggedFilters.length>0) {
            products = products.filter((product) => {
                let ret = false;
                const { tags } = product;
                for(let i=0;i<includeTaggedFilters.length;i++) {
                    const taggedFilter = includeTaggedFilters[i];
                    ret = ret || tagsMatch(tags,taggedFilter.tags);
                }
                return ret;
            });
        }        
        if (excludeTaggedFilters.length>0) {
            products = products.filter((product) => {
                let ret = true;
                const { tags } = product;
                for(let i=0;i<excludeTaggedFilters.length;i++) {
                    const taggedFilter = excludeTaggedFilters[i];
                    ret = ret && !tagsMatch(tags,taggedFilter.tags);
                }
                return ret;
            });
        }        

        if (sort==="alpha-asc") {
            products.sort(({caption: a},{caption: b}) => {
                if (a<b) return -1;
                if (a>b) return 1;
                return 0;
            });
        }
        else if (sort==="alpha-desc") {
            products.sort(({caption: a},{caption: b}) => {
                if (a<b) return 1;
                if (a>b) return -1;
                return 0;
            });
        }

        return products.filter(({visibility}) => visibility==="active");
    }

    getProductStubTable() {        
        const process = () => {            
            const ret: ProductStubTable = {};
            const optionSnapshot = this.getOptionsSnapshot();            
            const productTable = this.getProductTable();

            Object.keys(productTable).forEach((productId) => {
                const { id,caption } = productTable[productId];
                const { variants } = optionSnapshot.products[productId];                   

                ret[id] = {
                    _variants: variants,
                    id: id.toString(),
                    title: caption
                };                    
                ret[id]._variants.forEach((variant) => {                        
                    if (variant.signature.length>0) {                            
                        misc.combinations(variant.signature.length,(indexes) => {                                
                            let pnt: any = ret[id];
                            indexes.forEach((index) => {
                                const { id,value } = variant.signature[index];                                    
                                pnt[id] = pnt[id] || {};
                                pnt[id][value] = pnt[id][value] || {_variants: []};
                                pnt[id][value]._variants.push(variant);
                                pnt = pnt[id][value];
                            });
                        });
                    }
                });    
            });

            return ret;
        };

        return this._productStubTable = this._productStubTable || process();
    }

