    evalInContext(fnCode: string) {        
        const process = () => {
            const productAliasStubTable = this.getProductAliasStubTable();
            // console.log("productAliasStubTable",productAliasStubTable);
            const allVariants = (stub: any) => stub?._variants || [];
            const firstVariant = (stub: any) => [(stub?._variants || [])[0]].filter((x) => !!x);
            const globalVars = prebuildData.getGlobalConfig().vars;
            const fnFactory = Function("util","localStack",`with(localStack) { return ${fnCode}; }`);
            // console.log("fnCode",fnCode);
            return fnFactory(util,{...productAliasStubTable,allVariants,firstVariant,globalVars});
        };

        return this._evalInContextCache[fnCode] = this._evalInContextCache[fnCode] || process();
    }

    getProductSmallImageSlideShowMatch(productId: string,slideshowMatch?: string) {
        const product = this.getProductById(productId);
        if (product) {
            if (slideshowMatch) {
                for(let i=0;i<product.smallImages.length;i++) {
                    if (product.smallImages[i].src.indexOf(slideshowMatch)!==-1) {
                        const imageSrc = product.smallImages[i].src;
                        const originalSrc = product.smallImages[i].originalSrc;
                        return { imageSrc,originalSrc };
                    }                        
                }
            }
            const imageSrc = product.smallImages[0].src;
            const originalSrc = product.smallImages[0].originalSrc;
            return { imageSrc,originalSrc };
        }
        return null;
    }

    extractVariantsFromLink(productId: string,optionValues: CartItemOptionValues,optionId: string,link: string) {
        const optionsCompiler = prebuildData.getOptionsCompiler();

        const productOptions = optionsCompiler.getProductOptions(productId);
        const optionsStub: {[id: string]: {itemId: string}} = {};
        productOptions.options.forEach((option) => {
            const optionValue = optionValues[option.id];
            if ((option.type==="buttons") && (optionValue?.type==="buttons")) {
                optionsStub[option.id] = {itemId: optionValue.id};
            }
        });
        const option = {itemId: optionId}
        const fn = prebuildData.evalInContext(link);
        return [fn({options: optionsStub,option})].flat().map((link) => link?._variants?.[0]).filter((variant) => !!variant);
    }

