    getProductPriceTable() {
        const optionsCompiler = prebuildData.getOptionsCompiler();

        const process = () => {
            const ret: ProductPriceTable = {};
            const { products } = this.getOptionsSnapshot();
            Object.keys(products).forEach((productId) => {
                const { price,options } = optionsCompiler.getProductOptions(productId);
                const optionsStub: {[id: string]: {itemId: string}} = {};
                options.forEach((option) => {
                    if (option.type==="buttons") {
                        if (option.items.length>0) {
                            let selectedIndex = 0;
                            for(let i=0;i<option.items.length;i++) {
                                if (option.items[i].selected) {
                                    selectedIndex = i;
                                    break;
                                }
                            }
                            optionsStub[option.id] = {
                                itemId: option.items[selectedIndex].id
                            };
                        }
                    }
                });                

                let total = 0;

                const processPriceComponent = (priceDef: any,param: any = {}) => {
                    if (typeof priceDef==="string") {
                        let debug = false;
                        if (priceDef.startsWith("price.")) {
                            priceDef = `({price}) => ${priceDef}`;
                            debug = true;
                        }
                        const fn = this.evalInContext(priceDef);
                        const globalPriceObj = prebuildData.getGlobalConfig().vars.price || {};
                        const _param = isSimpleObject(param)?{...param,price: globalPriceObj}:{price: globalPriceObj};
                        const priceComponent = fn(_param);
                        if (typeof priceComponent==="number") {
                            total+= priceComponent;
                        }
                    }
                    else if (typeof priceDef==="number") {
                        total+= priceDef;
                    }
                };                

                processPriceComponent(price.base?price.base:0);                

                options.forEach((option) => {
                    if (option.type==="buttons") {
                        const optionStub = {
                            itemId: option.items[0].id
                        };
                        processPriceComponent(price[option.id]?.[option.items[0].id],{options: optionsStub,option: optionStub});
                    }
                    else if (option.type==="product-select") {
                        if ((!option.multiSelect) && (option.required)) {
                            processPriceComponent(price[option.id],{options: optionsStub,option: {}});
                        }
                        else if ((option.multiSelect) && (option.multiSelect.min>0)) {
                            processPriceComponent(price[option.id],{options: optionsStub,option: {multiSelect: {selectedCount: option.multiSelect.min}}});
                        }
                    }
                });

                ret[productId] = total;
            });            
            return ret;
        };

        const ret = this._productPriceTable = this._productPriceTable || process();
        // console.log("priceTable",ret);
        return ret;
    }

    getProductPrice(productId: string,asInteger = false) {
        const productPriceTable = this.getProductPriceTable();
        const ret = productPriceTable[productId] || 0;
        return asInteger?Math.round(ret*100):ret;
    }

    applyDiscountsToPrice(price: number,discounts: ProductDiscount[],freeProduct = false,freeComponentName?: string) {        
        const ret: { discountBreakdown: DiscountBreakdown,price: number } = {
            discountBreakdown: {},
            price
        };
        if (freeProduct) {
            ret.price = 0;
            ret.discountBreakdown[freeComponentName] = {
                discount: price,
                fromQuantity: 0
            };
            return ret;
        }
        const totalPercentage = discounts.reduce((acc,{percentage}) => acc+percentage,0);
        if (totalPercentage===0) return ret;
        ret.price = Math.round(((100-totalPercentage)/100)*price);
        ret.price = Math.min(price,Math.round(ret.price/50)*50);
        const totalDiscount = price-ret.price;
        let remainingDiscount = totalDiscount;
        discounts.forEach(({componentName,percentage},index) => {            
            const take = (index<discounts.length-1)?Math.floor((percentage/totalPercentage)*totalDiscount):remainingDiscount;
            remainingDiscount-= take;
            ret.discountBreakdown[componentName] = {
                discount: take,
                fromQuantity: 0
            };
        });        
        return ret;
    }

